/* Estilos globales para LoginForm */



/* Contenedor principal del formulario */
.login-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem; /* Más separación entre los elementos */
  }
  
  /* Estilo del título principal */
  .login-title {
    font-family: "DM Sans", serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    font-size: 32px;
    line-height: 41.66px;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: var(--color-primary-900);


  }
  
  /* Estilo del subtítulo */
  .login-subtitle {
    font-family: "DM Sans", serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    font-size: 24px;
    line-height: 41.66px;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: var(--color-neutral-600);
  }
  
  /* Contenedor de cada input */
  .login-input-group {
    position: relative;
    margin-bottom: 1rem; /* Espaciado entre campos */
  }
  
  /* Checkbox y enlace */
  .login-checkbox-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.875rem; /* text-sm */
    color: var(--color-primary-900);
  }
  
  /* Checkbox */
  .login-checkbox {
    accent-color: var(--color-base-100);; /* Cambia el color del checkbox */
    margin-right: 0.5rem;
  }
  
  /* Enlace "¿Olvidaste tu contraseña?" */
  .login-forgot-password {
    color: var(--color-neutral-600);
    font-weight: 500;
    text-decoration: none;
    transition: color 0.2s ease-in-out;
  }
  
  .login-forgot-password:hover {
    text-decoration: underline;
    color: var(--color-primary-900);
  }
  
  
  /* Botón de inicio de sesión */
  .button-login {
    font-family: "DM Sans", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 41.66px;
    text-align: center;
    background-color: transparent;
    border: 1.5px solid var(--primary-color); 
    color: var(--color-primary-900);
    border-radius: 8px;
    padding: 0.4rem;
    width: 100%;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  
  .button-login:hover {
    background-color: var(--primary-color); 
    color: var(--color-base-100); 
  }
  